import { PEDIDO_ANULADO, PEDIDO_APROBADO, PEDIDO_CERRADO, PEDIDO_CUMPLIDO, PEDIDO_INGRESADO, PEDIDO_INVALIDO } from "src/texts/pedidos";
import { EstadoPedido } from "../types";


export function getEstadoPedidoText(estado: EstadoPedido): string {
  switch (estado){
    case EstadoPedido.INGRESADO: return PEDIDO_INGRESADO;
    case EstadoPedido.APROBADO: return PEDIDO_APROBADO;
    case EstadoPedido.CUMPLIDO: return PEDIDO_CUMPLIDO;
    case EstadoPedido.CERRADO: return PEDIDO_CERRADO;
    case EstadoPedido.ANULADO: return PEDIDO_ANULADO;
    default: return PEDIDO_INVALIDO;
  }
}


export function numberToEstadoPedido(value: unknown): EstadoPedido {
  if (!Number.isFinite(value)) return EstadoPedido.INVALIDO;

  switch (value as number){
    case EstadoPedido.INGRESADO: return EstadoPedido.INGRESADO;
    case EstadoPedido.APROBADO: return EstadoPedido.APROBADO;
    case EstadoPedido.CUMPLIDO: return EstadoPedido.CUMPLIDO;
    case EstadoPedido.CERRADO: return EstadoPedido.CERRADO;
    case EstadoPedido.ANULADO: return EstadoPedido.ANULADO;
  }

  return EstadoPedido.INVALIDO;
}
