import { EstadoPedido } from "src/domain/pedido/types";


export function getEstadoPedidoColor(
  estado: EstadoPedido,
  suffix?: string
): string {
  let color: string;

  switch (estado){
    case EstadoPedido.INGRESADO: color='blue'; break;
    case EstadoPedido.APROBADO: color='purple'; break;
    case EstadoPedido.CUMPLIDO: color='green'; break;
    case EstadoPedido.CERRADO: color='black'; break;
    case EstadoPedido.ANULADO: color='red'; break;
    default: color='gray';
  }

  if (suffix==null) return color;
  
  return `${color}${suffix}`;
}
